import { Component, OnInit, Directive } from '@angular/core';
import { BsModalService, BsModalRef, ModalOptions } from 'ngx-bootstrap/modal';
import { Subject } from 'rxjs';

@Component({
    // tslint:disable-next-line: component-selector
    selector: 'confirm-component',
    templateUrl: './confirm.component.html'
})
export class ConfirmComponent {
    confirmResult: Subject<number>;
    bsModalRef: BsModalRef;

    constructor(private modalService: BsModalService) {
        modalService.onHide.subscribe(
            () => this.confirmResult.next(this.bsModalRef.content.result)
        );
    }

    openModalWithComponent(parms: ConfirmParms): Subject<number> {
        this.confirmResult = new Subject();

        this.bsModalRef = this.modalService.show(
            ModalContentComponent, {initialState: parms});

        // this.bsModalRef.content.closeBtnName = 'Close';

        return this.confirmResult;
    }
}

/* This is a component which we pass in modal*/

@Component({
    // tslint:disable-next-line: component-selector
    styles: [`
        .modal-body{
            font-weight: bolder;
            font-size: 1.2em;
            color: darkblue;
        }
    `],
    selector: 'modal-content',
    template: `
    <ul class="modal-body">
        <div *ngFor="let item of body">{{item}}</div>
    </ul>
    <div class="modal-footer">
        <button type="button" *ngIf="firstButton"
            class="btn btn-default" (click)="fnFirstButton()" >
            {{firstButton}}</button>
        <button type="button" *ngIf="secondButton"
            class="btn btn-default" (click)="fnSecondButton()" >
            {{secondButton}}</button>
        <button type="button" *ngIf="cancelButton"
            class="btn btn-primary" (click)="bsModalRef.hide()" >
            {{cancelButton}}</button>
    </div>
  `,
})
export class ModalContentComponent {
    result = 0;
    body: any[] = [];
    firstButton;
    secondButton;
    cancelButton;

    constructor(public bsModalRef: BsModalRef) {

    }

    fnFirstButton() {
        this.result = 1;
        this.bsModalRef.hide();
    }
    fnSecondButton() {
        this.result = 2;
        this.bsModalRef.hide();
    }

    // ngOnInit() {
    //     this.list.push('PROFIT!!!');
    // }
}
export class ConfirmParms {
    body: any[];
    firstButton?: string;
    secondButton?: string;
    cancelButton?: string;
}
