import { EventEmitter } from '@angular/core';
import { fn_box } from './utils';

export class Cell {
    private _value = ' ';
    private _isFixedValue = false;
    private _possibleValues = ['1', '2', '3', '4', '5', '6', '7', '8', '9'];
    private _notes = [];
    valueEmitter = new EventEmitter<string>();
    isFixedValueEmitter = new EventEmitter<boolean>();
    possibleValuesEmitter = new EventEmitter<string[]>();
    notesEmitter = new EventEmitter<string[]>();

    constructor(readonly row: number, readonly column: number, readonly box: number) {}

    get value() {
        return this._value;
    }
    set value(value: string) {
        this._value = value;
    }
    get isFixedValue() {
        return this._isFixedValue;
    }
    set isFixedValue(value) {
        this._isFixedValue = value;
    }
    get possibleValues() {
        return this._possibleValues;
    }
    set possibleValues(possibleValues: string[]) {
        this._possibleValues = possibleValues;
    }
    get notes() {
        return this._notes;
    }
    set notes(notes: string[]) {
        this._notes = notes;
    }

    inGroups(row: number, column: number) {
        return row === this.row || column === this.column || fn_box(row, column) === this.box;
    }
    hasPossibleValue(value: string): boolean {
        return this.possibleValues.indexOf(value) >= 0;
    }
    addPossibleValue(value: string) {
        this.possibleValues.push(value);
        this.possibleValues.sort();
    }
    removePossibleValue(value: string): boolean {
        const idx = this.possibleValues.indexOf(value);
        if (idx >= 0) {
            this.possibleValues.splice(idx, 1);
            if (this.removeNote(value))
                this.emitNotes();
            return true;
        }
        return false;
    }
    hasNote(note: string): boolean {
        return this.notes.indexOf(note) >= 0;
    }
    addNote(note: string) {
        this.notes.push(note);
        this.notes.sort();
    }
    removeNote(note: string): boolean {
        const idx = this.notes.indexOf(note);
        if (idx >= 0) {
            this.notes.splice(idx, 1);
            return true;
        }
        return false;
    }

    // emit

    emitValue() {
        this.valueEmitter.emit(this.value);
    }
    emitFixedValue() {
        this.isFixedValueEmitter.emit(this.isFixedValue);
    }
    emitPossibleValues() {
        this.possibleValuesEmitter.emit(this.possibleValues);
    }
    emitNotes() {
        this.notesEmitter.emit(this.notes);
    }

}
