import { Component, HostListener, ViewChild, ElementRef, AfterContentChecked, AfterContentInit} from '@angular/core';
import { HeaderService } from './header.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
    title = 'mgc';
    active = '#1a';
    headerHeight = '';
    @ViewChild('tabContent') tabContent: ElementRef;

    constructor(private headerService: HeaderService) {
        headerService.active.subscribe(
            (value: string) => {
                if (value.charAt(0) === '#') {
                    this.active = value;
                }
            }
        );
        headerService.height.subscribe(
            () => this.onResize(null)
        );
        headerService.getMarginTop.subscribe(
            () => this.headerService.marginTopEmiter.next(this.getMargin())
        );
    }

    @HostListener('window:resize', ['$event'])
    onResize(event) {
        const newSize = getComputedStyle(document.documentElement)
            .getPropertyValue('--min-width');
        if (newSize !== 'xs' && newSize !== 'sm')
            this.headerHeight = this.headerService.getHeight() + 'px';
        else
            this.headerHeight = '11em';
    }

    @HostListener('window:scroll', ['$event'])
    onScroll(event) {
        if (event.target.scrollingElement.scrollTop === 0) {
            this.headerService.scrollTop.emit(null);
        }
    }

    getMargin() {
        if (this.tabContent !== undefined)
            return this.tabContent.nativeElement.offsetTop;
        return 0;
    }
}
