import { Component, OnInit, Input, ViewChild, ViewEncapsulation } from '@angular/core';
import { ContextMenuComponent } from 'ngx-contextmenu';
import { StateService } from '../services/state.service';
import { StateEnum } from '../classes/state.enum';
import { Subject } from 'rxjs';

@Component({
    selector: 'app-cell',
    templateUrl: './cell.component.html',
    styleUrls: ['./cell.component.css']
})
export class CellComponent implements OnInit {

    constructor(private stateService: StateService) {
    }
    static settingNotes = new Subject<boolean>();
    @Input() readonly row: number;
    @Input() readonly column: number;
    @Input() readonly box: number;
    value = ' ';
    notes = '';
    isNote = false;
    possibleValues = [];
    isFixedValue = false;
    classes = {};
    hasContent = false;
    ctxToggleText = 'Notes';
    ctxNoteClass = {'cls-note': false};
    hasNoteToggle = false;

    @ViewChild(ContextMenuComponent, { static: true }) public basicMenu: ContextMenuComponent;

    ngOnInit(): void {
        this.stateService.board[this.row * 9 + this.column]
            .valueEmitter.subscribe(
            (val: string) => {
                this.value = val;
                if (this.stateService.state !== StateEnum.Solving) {
                    if (val === ' ')
                        this.classes['val-bold'] = false;
                    else
                        this.classes['val-bold'] = true;
                }
                this.isNote = false;
                this.setHasContent();
            }
        );
        this.stateService.board[this.row * 9 + this.column]
            .isFixedValueEmitter.subscribe(
            (val: boolean) => this.isFixedValue = val
        );
        this.stateService.board[this.row * 9 + this.column]
            .possibleValuesEmitter.subscribe(
            (val: string[]) => {
                this.possibleValues = [];
                for (const v of val)
                    this.possibleValues.push({ value: v });
            }
        );
        this.stateService.board[this.row * 9 + this.column]
            .notesEmitter.subscribe(
            (val: string[]) => {
                if (val.length) {
                    for (let i = 0; i < val.length; i++)
                        if (i === 0)
                            this.value = val[i];
                        else
                            this.value += ' ' + val[i];
                    this.isNote = true;
                } else {
                    this.value = ' ';
                    this.isNote = false;
                }
                this.setHasContent();
            }
        );
        this.stateService.stateEmitter.subscribe(
            (val: StateEnum) => this.hasNoteToggle = val === StateEnum.Solving
        );
        CellComponent.settingNotes.subscribe(
            (val: boolean) => {
                this.ctxNoteClass['cls-note'] = val;
                if (val)
                    this.ctxToggleText = 'Values';
                else
                    this.ctxToggleText = 'Notes';
            }
        );
        this.cellClasses();
    }
    setValue(value: string) {
        if (this.ctxNoteClass['cls-note'])
            this.stateService.setNote(this.row, this.column, value);
        else
            this.stateService.setValue(this.row, this.column, value);
    }
    setHasContent() {
        if (this.value !== ' ')
            this.hasContent = true;
        else
            this.hasContent = false;
    }
    noteToggle() {
        CellComponent.settingNotes.next(!this.ctxNoteClass['cls-note']);
    }
    // noteToggle() {
    //     this.settingNotes = !this.settingNotes;
    //     if (this.settingNotes){
    //         this.ctxItemClasses['cls-note'] = true;
    //         this.ctxToggleClasses['cls-note'] = true;
    //         this.ctxToggleText = 'Values';
    //     } else {
    //         this.ctxItemClasses['cls-note'] = false;
    //         this.ctxToggleClasses['cls-note'] = false;
    //         this.ctxToggleText = 'Notes';
    //     }
    // }
    setFixedValue(value: boolean) {
        this.isFixedValue = value;
    }
    cellClasses() {
        const classes = {'cls-cell': true};
        if ( (this.column + 1) % 3 === 0) {
            classes['right-bold'] = true;
        }
        if (this.column === 0) {
            classes['left-bold'] = true;
        }
        if ( (this.row + 1) % 3 === 0) {
            classes['bottom-bold'] = true;
        }
        if (this.row === 0) {
            classes['top-bold'] = true;
        }
        classes['val-bold'] = false;
        this.classes = classes;
    }
    // hasNoteToggle() {
    //     const element = document.getElementsByTagName('body');
    //     const size = window.getComputedStyle(element[0]).getPropertyValue('--min-width');
    //     if (size === 'lg' || size === 'xl' || this.stateService.state !== StateEnum.Solving)
    //         return false;
    //     else
    //         return true;
    // }
}
