import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { StateService } from '../services/state.service';
import { StateEnum } from '../classes/state.enum';
import { ConfirmComponent } from 'src/app/confirm/confirm.component';
import { Subject, Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import {
    MatDialog,
    MatDialogRef,
    MAT_DIALOG_DATA,
} from '@angular/material/dialog';
import {
    FormControl,
    Validators,
    AbstractControl,
    ValidatorFn,
} from '@angular/forms';

@Component({
    selector: 'app-controls',
    templateUrl: './controls.component.html',
    styleUrls: ['./controls.component.css'],
})
export class ControlsComponent implements OnInit {
    constructor(private stateService: StateService, public dialog: MatDialog) {
        this.stateService.stateEmitter.subscribe(
            (val: number) => (this.stateString = StateEnum[val])
        );
        this.stateService.messageEmitter.subscribe((val: string[]) =>
            this.confirmComponent.openModalWithComponent({
                body: val,
                firstButton: 'Ok',
            })
        );
    }
    confirmResult: Subject<number>;
    stateString: string;
    multipleSolutions = false;
    numberOfSolutions = 0;
    solutionOrder = 0;
    sudokuName = '';
    @ViewChild(ConfirmComponent) confirmComponent: ConfirmComponent;

    ngOnInit(): void {
        this.stateString = StateEnum[this.stateService.state];
    }

    // Empty Board

    manualEdition() {
        this.stateService.manualEdition();
    }
    mediumTemplate() {
        this.stateService.mediumTemplate();
    }
    hardTemplate() {
        this.stateService.hardTemplate();
    }
    hardestTemplate() {
        this.stateService.hardestTemplate();
    }
    withoutSolutionTemplate() {
        this.stateService.withoutSolutionTemplate();
    }
    multipleSolutionsTemplate() {
        this.stateService.multipleSolutionsTemplate();
    }

    // Editing

    clearAll() {
        this.confirmResult = this.confirmComponent.openModalWithComponent({
            body: ['Clear all?'],
            firstButton: 'yes',
            cancelButton: 'no',
        });
        this.confirmResult.subscribe((result) => {
            if (result) this.stateService.clearAll();
            this.confirmResult.unsubscribe();
        });
    }
    clearAllDisabled(): boolean {
        return this.stateService.fixedValues < 1;
    }
    finishEdition() {
        this.stateService.finishEdition();
    }
    finishEditionDisabled(): boolean {
        return this.stateService.fixedValues < 17;
    }

    // Solving

    editTemplate() {
        if (!this.stateService.hasValuesSetted()) {
            this.multipleSolutions = false;
            this.stateService.editTemplate();
            return;
        }
        this.confirmResult = this.confirmComponent.openModalWithComponent({
            body: ['Clear all established values?'],
            firstButton: 'yes',
            cancelButton: 'no',
        });
        this.confirmResult.subscribe((result) => {
            if (result) {
                this.multipleSolutions = false;
                this.stateService.editTemplate();
            }
            this.confirmResult.unsubscribe();
        });
    }
    calculateSolution() {
        if (!this.stateService.hasValuesSetted()) {
            this.numberOfSolutions = this.stateService.calculateSolution(false);
            this.multipleSolutions = this.numberOfSolutions > 1;
            return;
        }
        this.confirmResult = this.confirmComponent.openModalWithComponent({
            body: ['Clear all established values?'],
            firstButton: 'Yes',
            secondButton: 'No',
            cancelButton: 'Cancel',
        });
        this.confirmResult.subscribe((result) => {
            if (result > 0) {
                this.numberOfSolutions = this.stateService.calculateSolution(
                    result === 1
                );
                this.multipleSolutions = this.numberOfSolutions > 1;
            }
            this.confirmResult.unsubscribe();
        });
    }
    solutionReload() {
        this.stateService.loadSolution(this.solutionOrder);
    }
    solutionUp() {
        this.solutionOrder++;
        if (this.solutionOrder >= this.numberOfSolutions)
            this.solutionOrder = 0;
        this.stateService.loadSolution(this.solutionOrder);
    }
    solutionDown() {
        this.solutionOrder--;
        if (this.solutionOrder < 0)
            this.solutionOrder = this.numberOfSolutions - 1;
        this.stateService.loadSolution(this.solutionOrder);
    }

    // load/save

    load() {
        const name = '';
        let records: [string, Date, Date][] = JSON.parse(
            localStorage.getItem('@@@names')
        );
        if (records === null) records = Array();
        const dialogRef = this.dialog.open(NameDialog, {
            width: '400px',
            data: { name, records, save: true, rewrite: true },
        });

        dialogRef.afterClosed().subscribe((result) => {
            if (result !== undefined) {
                localStorage.setItem('@@@' + result, JSON.stringify(this.stateService.getSudoku()));
                for (const record of records)
                    if (record[0] === result)
                        record[2] = new Date();
                localStorage.setItem('@@@names', JSON.stringify(records));
            }
        });
    }
    save() {
        const name = '';
        let records: [string, Date, Date][] = JSON.parse(
            localStorage.getItem('@@@names')
        );
        if (records === null) records = Array();
        const dialogRef = this.dialog.open(NameDialog, {
            width: '400px',
            data: { name, title: 'Name for save', records, hasInput: true, hasAutocomplete: false, load: false },
        });

        dialogRef.afterClosed().subscribe((result) => {
            if (result !== undefined) {
                localStorage.setItem('@@@' + result, JSON.stringify(this.stateService.getSudoku()));
                const date = new Date();
                records.push([result, date, date]);
                localStorage.setItem('@@@names', JSON.stringify(records));
            }
        });
    }

    loadOverwrite(load: boolean) {
        const name = '';
        let records: [string, Date, Date][] = JSON.parse(
            localStorage.getItem('@@@names')
        );
        if (records === null) records = Array();
        const dialogRef = this.dialog.open(NameDialog, {
            width: '400px',
            data: { name, title: (load ? 'Sudoku name to load' : 'Sudoku name to overwrite'),
                    records, hasInput: true, hasAutocomplete: true, load },
        });

        dialogRef.afterClosed().subscribe((result) => {
            if (result !== undefined) {
                if (load) {
                    this.sudokuName = result;
                    this.stateService.setSudoku(JSON.parse(localStorage.getItem('@@@' + result)));
                    return;
                }
                localStorage.setItem('@@@' + result, JSON.stringify(this.stateService.getSudoku()));
                for (const record of records)
                    if (record[0] === result)
                        record[2] = new Date();
                localStorage.setItem('@@@names', JSON.stringify(records));
            }
        });
    }
}
export interface DialogData {
    name: string;
    title: string;
    records: [string, Date, Date][];
    hasInput: boolean;
    hasAutocomplete: boolean;
    load: boolean;
}
@Component({
    // tslint:disable-next-line: component-selector
    selector: 'name-dialog',
    template: `
        <div class="dialog">
            <div mat-dialog-content>
                <mat-form-field *ngIf="data.hasInput">
                    <mat-label>{{data.title}}</mat-label>
                    <input
                        matInput
                        [(ngModel)]="data.name"
                        [formControl]="nameCtrl"
                        required
                        [matAutocomplete]="auto"
                    />
                    <mat-autocomplete #auto="matAutocomplete">
                        <mat-option *ngFor="let record of filteredOptions | async" [value]="record[0]">
                            {{ record[0] }}&nbsp;
                            <span style="color: green; float: right;">{{record[1] | date}}&nbsp;{{record[2] | date}}</span>
                        </mat-option>
                    </mat-autocomplete>
                    <mat-error *ngIf="nameCtrl.invalid">{{
                        getErrorMessage()
                    }}</mat-error>
                </mat-form-field>

                <!-- <label id="radio-group-label" *ngIf="!data.save">Select a record</label>
                <mat-radio-group
                    *ngIf="!data.save"
                    aria-labelledby="radio-group-label"
                    class="radio-group"
                    [(ngModel)]="favoriteSeason">
                    <mat-radio-button class="radio-button" *ngFor="let record of records" [value]="season">
                        {{season}}
                    </mat-radio-button>
                </mat-radio-group> -->
            </div>
            <div mat-dialog-actions>
                <button mat-button (click)="onNoClick()">No Thanks</button>
                <button
                    mat-button
                    [mat-dialog-close]="data.name"
                    cdkFocusInitial
                    [disabled]="!nameCtrl.valid"
                >
                    Ok
                </button>
            </div>
        </div>
        `,
    styles: [
        `
            .mat-dialog-container {
                min-width: 450px;
                max-width: 500px;
                width: 100%;
            }
            .mat-option.mat-active,
            .mat-option.mat-selected:not(.mat-option-multiple):not(.mat-option-disabled) {
                background: rgba(0,0,0,.1);
                color: rgba(0,0,0,1);
            }
            .mat-option:hover:not(.mat-option-disabled) {
                background-color: lightcyan;
            }
            mat-form-field {
                width: 100%;
            }
        `,
    ],
})
// tslint:disable-next-line: component-class-suffix
export class NameDialog implements OnInit {
    nameCtrl: FormControl;
    filteredOptions: Observable<[string, Date, Date][]>;
    constructor(
        public dialogRef: MatDialogRef<NameDialog>,
        @Inject(MAT_DIALOG_DATA) public data: DialogData
    ) {
        this.nameCtrl = new FormControl('', [
            Validators.required,
            this.nameValidator(data.records)
        ]);
    }
    ngOnInit(): void {
        if (!this.data.hasAutocomplete)
            return;
        this.filteredOptions = this.nameCtrl.valueChanges.pipe(
            startWith(''),
            map(value => this._filter(value))
        );
    }
    private _filter(value: string): [string, Date, Date][] {
        const filterValue = value.toLowerCase();
        return this.data.records.filter(record => record[0].toLowerCase().includes(filterValue));
    }
    onNoClick(): void {
        this.dialogRef.close();
    }
    getErrorMessage() {
        if (this.nameCtrl.hasError('required')) {
            return 'You must enter a value';
        }
        return this.nameCtrl.hasError('nameOk')
            ? 'Not a valid name'
            : this.nameCtrl.hasError('nameRepeated')
            ? 'Repeated name'
            : this.nameCtrl.hasError('nameNotExists')
            ? 'The name does not exists'
            : '';
    }
    nameValidator(records: [string, Date, Date][]): ValidatorFn {
        return (
            control: AbstractControl
        ): { [key: string]: boolean } | null => {
            if (control.value === '@@@names')
                return { nameKo: true };
            if (!this.data.hasAutocomplete && !this.data.load)
                for (const record of records)
                    if (record[0] === control.value)
                        return { nameRepeated: true };
            if (this.data.hasAutocomplete && this.data.load) {
                for (const record of records)
                    if (record[0] === control.value)
                        return null;
                return { nameNotExists: true };
            }
            return null;
        };
    }
}
