import { Injectable, Output, EventEmitter } from '@angular/core';
import { BehaviorSubject, Subject } from 'rxjs';

@Injectable({
    providedIn: 'root'
})
export class HeaderService {
    public height = new BehaviorSubject<any>({});
    @Output() public active = new EventEmitter<string>();
    @Output() public getMarginTop = new EventEmitter<null>();
    public marginTopEmiter = new Subject<number>();
    public scrollTop = new EventEmitter<null>();

    getHeight(): number {
        const sizes = ['xs', 'sm', 'md', 'lg', 'xl'];
        const min = getComputedStyle(document.documentElement)
            .getPropertyValue('--min-width');
        const height = this.height.getValue();
        let value = 0;
        // tslint:disable-next-line: forin
        for (const size of sizes) {
            if (height[size] !== undefined)
                value = height[size];
            if (size === min)
                break;
        }
        return value;
    }

}
