import { Injectable, EventEmitter } from '@angular/core';
import { StateEnum } from '../classes/state.enum';
import { Cell } from '../classes/cell';
import { fn_box } from '../classes/utils';
import { SetValueService } from './set-value.service';
import { HardSolutionService } from './hard-solution.service';
import { calculate } from './calculate';
import { TEMPLATES } from 'src/db-data';


@Injectable({
    providedIn: 'root',
})
export class StateService {
    state = StateEnum['Empty Board'];
    stateEmitter = new EventEmitter<StateEnum>();
    messageEmitter = new EventEmitter<string[]>();
    board: Cell[] = [];
    fixedValues = 0;
    solutions: [number, number, string][][] = new Array();
    numberOfSolutions = 0;
    fn_box = fn_box;
    private rows: Cell[][] = new Array();
    private columns: Cell[][] = new Array();
    private boxes: Cell[][] = new Array();

    constructor(private setValueService: SetValueService,
                private hardSolutionService: HardSolutionService) {
        this.emitState();
        for (let r = 0; r < 9; r++)
            for (let c = 0; c < 9; c++)
                this.board.push(new Cell(r, c, fn_box(r, c)));
    }

    private setTemplate(template: string[][]) {
        this.manualEdition();
        template.forEach(
            (rowArray, row) => rowArray.forEach(
                (value, column) => {
                    if (value !== ' ')
                        this.setValue(row, column, value);
                }
            )
        );
    }
    private emitAllPossibleValues() {
        for (const cell of this.board)
            cell.emitPossibleValues();
    }
    private emitState() {
        this.stateEmitter.emit(this.state);
    }

    setValue(row: number, column: number, newValue: string) {
        this.fixedValues = this.setValueService.setValue(this.board, this.state, this.fixedValues, row, column, newValue);
    }

    setNote(row: number, column: number, newNote: string) {
        this.fixedValues = this.setValueService.setNote(this.board, this.state, this.fixedValues, row, column, newNote);
    }

    // Empty Board

    manualEdition() {
        this.state = StateEnum.Editing;
        this.emitState();
        this.emitAllPossibleValues();
    }
    mediumTemplate() {
        this.setTemplate(TEMPLATES.MEDIUM_TEMPLATE);
    }
    hardTemplate() {
        this.setTemplate(TEMPLATES.HARD_TEMPLATE);
    }
    hardestTemplate() {
        this.setTemplate(TEMPLATES.HARDEST_TEMPLATE);
    }
    withoutSolutionTemplate() {
        this.setTemplate(TEMPLATES.WITHOUT_SOLUTION_TEMPLATE);
    }
    multipleSolutionsTemplate() {
        this.setTemplate(TEMPLATES.MULTIPLE_SOLUTIONS_TEMPLATE);
    }

    // Editing

    clearAll() {
        this.state = StateEnum['Empty Board'];
        this.emitState();
        this.fixedValues = 0;
        for (const cell of this.board) {
            cell.value = ' ';
            cell.possibleValues = [];
            cell.notes = [];
            cell.emitValue();
            cell.emitPossibleValues();
            cell.emitNotes();
            cell.possibleValues =  ['1', '2', '3', '4', '5', '6', '7', '8', '9'];
        }
    }
    finishEdition() {
        this.state = StateEnum.Solving;
        this.emitState();
        for (const cell of this.board) {
            if (cell.value !== ' ') {
                cell.isFixedValue = true;
                cell.emitFixedValue();
            }
        }
    }

    // Solving

    hasValuesSetted(): boolean {
        for (const cell of this.board)
            if (!cell.isFixedValue && cell.value !== ' ')
                return true;
        return false;
    }

    editTemplate() {
        for (const cell of this.board) {
            if (cell.isFixedValue) {
                cell.isFixedValue = false;
                cell.emitFixedValue();
            } else if (cell.value !== ' ') {
                this.setValue(cell.row, cell.column, ' ');
            }
        }
        // Changing the state at the end prevents updating the fixed value counter
        this.state = StateEnum.Editing;
        this.emitState();
    }

    calculateSolution(clearValues: boolean): number {
        let solved = true;
        if (clearValues) {
            for (const cell of this.board) {
                if (!cell.isFixedValue && cell.value !== ' ') {
                    this.setValue(cell.row, cell.column, ' ');
                }
            }
        }

        calculate(this.board);
        for (const cell of this.board)
            if (cell.value === ' ')
                solved = false;
        if (solved)
            return 1;

        this.solutions = this.hardSolutionService.solve(this.board);
        console.log(this.solutions);
        this.numberOfSolutions = this.solutions.length;
        if (this.numberOfSolutions === 0)
            this.messageEmitter.emit(['Sudoku without solutions']);
        if (this.numberOfSolutions === 1)
            this.loadSolution(0);
        if (this.numberOfSolutions <= 1)
            return this.numberOfSolutions;
        this.loadSolution(0);
        if (this.numberOfSolutions > 10){
            delete this.solutions[10];
            this.numberOfSolutions = 10;
            this.messageEmitter.emit([
                'Sudoku with more than 10 solutions.',
                'Only the first ten solutions are loaded.']);
        }
        return this.numberOfSolutions;
    }

    loadSolution(num: number) {
        for (const value of this.solutions[num])
            this.setValue(value[0], value[1], value[2]);
    }

    // saving

    // Row, column, isFixedValue, value, notes
    getSudoku(): [number, number, boolean, string, string[]][] {
        const sudoku: [number, number, boolean, string, string[]][] = [];
        for (const cell of this.board) {
            if (cell.value === ' ' && !cell.notes.length)
                continue;
            sudoku.push([cell.row, cell.column, cell.isFixedValue, cell.value, cell.notes]);
        }
        return sudoku;
    }
    setSudoku(sudoku: [number, number, boolean, string, string[]][]) {
        this.clearAll();
        this.state = StateEnum.Editing;
        this.emitState();
        for (const cellData of sudoku)
            if (cellData[2])
                this.setValue(cellData[0], cellData[1], cellData[3]);
        this.finishEdition();
        for (const cellData of sudoku)
            if (!cellData[2] && cellData[3] !== ' ')
                this.setValue(cellData[0], cellData[1], cellData[3]);
            else if (cellData[4].length) {
                this.board[cellData[0] * 9 + cellData[1]].notes = cellData[4];
                this.board[cellData[0] * 9 + cellData[1]].emitNotes();
            }
    }
}
