import { Injectable } from '@angular/core';
import { Cell } from '../classes/cell';
import { StateEnum } from '../classes/state.enum';

@Injectable({
    providedIn: 'root',
})
export class SetValueService {
    constructor() {}

    setValue(board: Cell[], state: StateEnum, fixedValues: number, row: number, column: number, newValue: string): number {
        const settedCell = board[row * 9 + column];
        const oldValue = settedCell.value;
        if (settedCell.notes.length) {
            settedCell.notes = [];
            settedCell.emitNotes();
        }
        // New value must be setted before isPossibleValue calls
        settedCell.value = newValue;
        settedCell.emitValue();
        if (state === StateEnum.Editing) {
            if (oldValue === ' ')
                fixedValues++;
            else if (newValue === ' ')
                fixedValues--;
        }
        for (const cell of board) {
            if (cell.inGroups(row, column)) {
                if (oldValue !== ' ' && this.isPossibleValue(board, cell, oldValue))
                    cell.addPossibleValue(oldValue);
                if (newValue !== ' ') cell.removePossibleValue(newValue);
                cell.emitPossibleValues();
            }
        }
        return fixedValues;
    }

    private isPossibleValue(board: Cell[], settedCell: Cell, newValue: string): boolean {
        for (const cell of board) {
            if (cell.inGroups(settedCell.row, settedCell.column)
                && cell.value === newValue)
                return false;
        }
        return true;
    }

    setNote(board: Cell[], state: StateEnum, fixedValues: number, row: number, column: number, newNote: string): number {
        const settedCell = board[row * 9 + column];

        const oldValue = settedCell.value;
        if ( settedCell.value !== ' ')
            fixedValues = this.setValue(board, state, fixedValues, row, column, ' ');

        if (newNote === ' ' && oldValue !== ' ')
            settedCell.addNote(oldValue);
        else if (newNote === ' ')
            settedCell.notes = [];
        else if (settedCell.hasNote(newNote))
            settedCell.removeNote(newNote);
        else
            settedCell.addNote(newNote);
        settedCell.emitNotes();

        return fixedValues;
    }
}
